.cursor {
  height: 24px;
  width: 24px;
  border-radius: 50%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  mix-blend-mode: difference;
  background-color: rgb(255, 255, 255);
  pointer-events: none;

  @media screen and (max-width: 1000px){
    display: none;
    
  }
}

.cursor-eye {
  background-image: url(../../images/eye.svg);
  background-repeat: no-repeat;
  background-size:  80%;
  background-position: center;
  background-color: transparent;
}

.cursor-pensil {
  background-image: url(../../images/pensil.svg);
  background-repeat: no-repeat;
  background-size:  50%;
  background-position: center;
  background-color: transparent;
}